<template>
	<div>
		<Card :bordered="true" style="width:400px;display: inline-block;">
			<div>
				<div class="horizontal">
					<Avatar shape="circle" icon="ios-people" :src="iconUrl" size=100 />
				</div>
				<div class="horizontal">
					<h1>{{ username }}</h1>
					<p>{{ description }}</p>
				</div>
			</div>
		</Card>
		<ButtonGroup vertical style='display: inline-block;'>
			<Button icon="md-people"></Button>
			<Button icon="md-cut" v-on:click='deleteSubscription()'></Button>
		</ButtonGroup>
	</div>	
</template>

<script>
	//import data from '../mock/deleteSubscription.js'
	export default{
		methods:{
			deleteSubscription(){
				var url = 'http://localhost:8080/user/follow?targetGlobalUserName='+this.username
				this.$axios
					.delete(url)
					.then(response=>{
						this.$emit('deleteSubscription',this.username)
						this.$Message.success('Success')
					})
					.catch(error=>{
						this.$Message.error('Fail')
						console.log(error)
					})
			}
		},
		props:{
			username: String,
			description: String,
			iconUrl: String
		}
	}
</script>

<style>
	.horizontal{
		display: inline-block;
		margin: 10px;
	}
</style>
